.header {
  height: 250px;
  background-image: url(../../images/4ea7fb833c63a4ea0b0fb696c5919dd7.png);
  background-size: 450px 250px;
  background-position: center;
  position: relative;
}
.logo {
  width: 200px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -125%);
  box-shadow: 0px 0px 1px 1px #32eb4bf1;
}

.pulse {
  animation: pulse-animation 2s infinite;
}

@keyframes pulse-animation {
  0% {
    transform: scale(-0.5);
    box-shadow: 0 0 0 0px rgba(40, 228, 15, 0.836);
  }
  100% {
    transform: scale(0.5);
    box-shadow: 0 0 0 20px rgba(40, 228, 15, 0.836);
  }
}
